import React, { component } from "react";

export default class Gallery extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      extend: false,
      image: ""
    };
  }
  handleExtend = image => {
    this.setState({ extend: true, image });
  };
  handleCollapse = () => {
    this.setState({ extend: false });
  };
  render() {
    if (this.state.extend) {
      return (
        <div>
          <button>Next</button>
          <img src={"/static/gallery" + this.state.image} />
          <button>Last</button>
          <button onClick={this.handleCollapse}>X</button>
        </div>
      );
    } else {
      return (
        <div className="masonry">
          {this.props.images.map((image, index) => (
            <img
              onClick={() => this.handleExtend(image)}
              key={index}
              className="item"
              src={"/static/gallery/" + image}
            />
          ))}

          <style jsx>{`
            .masonry {
              column-count: 4;
              column-gap: 0.5em;
            }
            .item {
              background-color: #eee;
              display: inline-block;
              margin: 0 0 0.5em;
              width: 100%;
            }
          `}</style>
        </div>
      );
    }
  }
}
